import interfaces from '../../utils/interfaces.js';
var common = require('../../utils/common.js');
import app from "../../App.vue";
export default {
	data() {
		return {
			user_info: '',
			token:'',
			address:'',
			img_list: [], //照片数组
			img_num: 0, //照片的数量
			multiArray: [
				[],
				[]
			], //上门时间数组
			multiIndex: [0, 0],
			server_name: '请选择服务类型',
			server_id: 0,
			prohibit: false,
			type:'',
			good_id:'',
			good:[]
		}
	},
	onLoad(options) {
		this.type=options.type;
		this.good_id=options.good_id;

		//从0天开始循环出之后7天
		for (let i = 0; i < 7; i++) {
			this.getDay(i)
		}
		this.getHours();
		
	},
	onShow() {
		this.user_info = app.globalData.user_info;
		if (this.user_info) {
			this.token = app.globalData.user_info.token;
			this.request({
				url: interfaces.addressDefault,
				header:{
					token:this.token
				},
				method: 'POST',
				success: res => {
					//console.log(res);
					if (res.code == 200) {
						if(!common.isBlank(res.data)){
							this.address= res.data;
						}
					} else {
						app.isLogin(res.code, res.msg);
					}
				}
			});
			this.getGoodDetail();
		} else {
			wx.reLaunch({
				url: '/pages/auth/index',
			})
		}
	},
	methods: {
		getGoodDetail() {
			this.request({
				url: interfaces.goodDetail,
				data: {
					id: this.good_id
				},
				method:"post",
				success: res => {
					if(res.code==200){
						this.good=res.data;
					}
				}
			})
		},
		goBack(num){
			wx.navigateBack({
				delta: num
			})
		},
		getDay(day) {
			var today = new Date();
			var targetday_milliseconds = today.getTime() + 1000 * 60 * 60 * 24 * day;
			today.setTime(targetday_milliseconds); //注意，这行是关键代码
			var tYear = today.getFullYear();
			var tMonth = today.getMonth();
			var tDate = today.getDate();
			tMonth = this.doHandleMonth(tMonth + 1);
			tDate = this.doHandleMonth(tDate);

			this.multiArray[0].push(tYear + "-" + tMonth + "-" + tDate);
		},
		getHours() {
			var today = new Date();
			var tHours = today.getHours();
			for (let i = tHours; i <= 22; i = i + 2) {
				this.multiArray[1].push(i + ":00~" + (i + 2) + ":00");
			}
		},
		//当长度为1时，前面加个0，例如 7月改称07月
		doHandleMonth(month) {
			var m = month;
			if (month.toString().length == 1) {
				m = "0" + month;
			}
			return m;
		},

		bindMultiPickerChange(e) {
			//console.log(e);
			this.multiIndex=e.detail.value;
		},

		//跳转选择地址页面
		select() {
			if(!common.isBlank(this.address)){
				wx.navigateTo({
					url: '../address/select',
				})
			}else{
				wx.navigateTo({
					url: '../address/add',
				})
			}	
		},

		//参数一 e 数据源
		//参数二 t 成功success的数量
		//参数三 a 失败fail的数量
		//参数四 i 完成comlete的数量
		//参数五 n 要上传的图片数量
		uploadDIY: function(e, t, a, i, n) {
			var o = this,
				s = this;
			wx.uploadFile({
				url: interfaces.orderUpload,
				filePath: e[i],
				name: "file",
				header: {
					'content-type': 'application/json',
					token:s.token
				},
				success: function(e) {
					e.data=JSON.parse(e.data);
					var num = s.img_list.length + 1;
					t++;
					s.img_list.push(e.data);
					s.img_num = num;
				},
				fail: function(e) {
					a++;
				},
				complete: function() {
					++i == n ? wx.hideLoading() : o.uploadDIY(e, t, a, i, n);
				}
			});
		},
		choosePic: function() {
			var a = this;
			wx.chooseImage({
				count: 6,
				sizeType: ["compressed"],
				sourceType: ["album", "camera"],
				success: function(e) {
					e.tempFilePaths;
					var t = e.tempFilePaths.length;
					a.uploadDIY(e.tempFilePaths, 0, 0, 0, t);
				}
			});
		},

		//表单提交事件
		formSubmit: function(e) {

			var data = e.detail.value;
			data.good_title = this.good.title;
			data.good_id = this.good.id;
			data.type=this.type;
			
			if (common.isBlank(this.address)) {
				uni.showToast({
					title: '请选择服务地址',
					icon: "none",
				});
				return;
			} else {
				data.linkman = this.address.linkman;
				data.mobile = this.address.mobile;
				data.position_address = this.address.position_address;
				data.address = this.address.address;
				data.position_city = this.address.position_city;
				data.longitude = this.address.longitude;
				data.latitude = this.address.latitude;
			}

			if (common.isBlank(data.remark)) {
				wx.showToast({
					title: '请描述报修故障及内容',
					icon: "none",
				});
				return;
			}
			if (data.good_id == 0) {
				wx.showToast({
					title: '请选择服务项目',
					icon: "none",
				});
				return;
			}


			data.customer_time = this.multiArray[0][this.multiIndex[0]] + " " + this.multiArray[1][this.multiIndex[1]];
			
			var pics = this.img_list;
			data.pics = pics.join();
			this.prohibit= true;
			
			this.request({
				url: interfaces.orderAdd,
				data: data,
				header: {
					token:this.token	
				},
				method: 'POST',
				success: res => {
					console.log(res);
					if (res.code == 200) {
						wx.switchTab({
							url: 'index',
						})
					} else {
						app.isLogin(res.code, res.msg);
					}
				}
			});
		},

		//点击报修图片预览图片事件
		preImage(url) {
			wx.previewImage({
				current: url,
				urls: this.img_list,
			})
		},

		//删除订单图片
		picsDelete(index) {
			this.img_list.splice(index, 1);
			this.img_num--;
		}
	}
}
